.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    text-align: center;
  }
  
  h1 {
    margin-bottom: 20px;
  }
  
  .search-form {
    margin-bottom: 20px;
  }
  
  .gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 20px;
    margin-bottom: 20px;
  }
  
  .photo-card {
    background-color: #f5f5f5;
    padding: 10px;
    text-align: left;
  }
  
  .photo-card img {
    width: 100%;
    height: auto;
    margin-bottom: 10px;
  }
  
  .info {
    font-size: 14px;
  }
  
  .info-item {
    margin: 5px 0;
  }
  
  .load-more {
    display: none;
  }
  