.container {
  max-width: 800px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

h1, .search-form {
  margin-bottom: 20px;
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin-bottom: 20px;
  display: grid;
}

.photo-card {
  text-align: left;
  background-color: #f5f5f5;
  padding: 10px;
}

.photo-card img {
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.info {
  font-size: 14px;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  display: none;
}

